<template>
	<layout>
		<div class="d-flex">
			<h1 class="text-h4"></h1>
			<v-spacer></v-spacer>
			<v-btn color="primary" dark class="mb-1 ms-2" @click="formDialog = true">
				<v-icon class="d-none d-sm-block me-2">mdi-plus</v-icon>
				<v-icon class="d-sm-none d-block">mdi-plus</v-icon>
				<span class="d-none d-sm-block">   </span>
			</v-btn>
		</div>
		<v-divider class="my-4"></v-divider>
		<v-card>
			<v-toolbar flat color="white">
				<v-text-field v-model="search" label="" dense outlined single-line hide-details append-icon="mdi-magnify"></v-text-field>
				<v-spacer></v-spacer>
				<v-btn color="primary" outlined class="mb-1 ms-2" :disabled="selected.length === 0" @click="openInvoice">
					<v-icon>mdi-plus</v-icon>
					<span class="d-none d-sm-block">  </span>
				</v-btn>

				<v-dialog v-model="formDialog" max-width="700px" @click:outside="closeForm">
					<agent-form
						ref="agentForm"
						:agent="updateAgent"
						:form-title="formTitle"
						:is-edit="isEditMode"
						@canceled="closeForm"
						@submited="saveAgent"
					></agent-form>
				</v-dialog>
				<v-dialog v-model="invoiceDialog" @click:outside="closeInvoiceForm" fullscreen>
					<invoice-form
						:key="invoiceKey"
						ref="invoiceForm"
						:agents="selectedAgents"
						@canceled="closeInvoiceForm"
						@submited="saveInvoice"
					></invoice-form>
				</v-dialog>
			</v-toolbar>
		</v-card>
		<v-data-table
			v-model="selected"
			:headers="headers"
			:loading="isLoading"
			:items="agents"
			sort-by="id"
			sort-desc
			class="mt-8 elevation-16"
			:search="search"
			group-by="company"
			show-select
		>
			<template v-slot:[`group.header`]="{ group }">
				<td colspan="7">
					{{ group !== 'NO' ? group : '' }}
				</td>
			</template>
			<template v-slot:[`item.actions`]="{ item }">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-btn dark small min-width="40" color="success" @click="editAgent(item)" v-on="on">
							<v-icon size="18">mdi-pencil</v-icon>
						</v-btn>
					</template>
					<span>  </span>
				</v-tooltip>
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-btn dark small min-width="40" class="mx-3" color="error" @click="deleteAgent(item)" v-on="on">
							<v-icon size="18">mdi-delete</v-icon>
						</v-btn>
					</template>
					<span></span>
				</v-tooltip>
			</template>
		</v-data-table>
		<confirm-dailog ref="confirm"></confirm-dailog>
	</layout>
</template>

<script>
// import Vue from 'vue';
import AgentForm from '../components/agents/agent-form.vue';
import ConfirmDailog from '../components/base/confirm-dailog';
import Layout from './layout/Layout.vue';
import { agentsComputed, agentsActions, invoicesActions } from '../state/mapper';
import InvoiceForm from '../components/agents/invoice-form.vue';
import printJS from 'print-js';

export default {
	name: 'Agents',

	components: { Layout, AgentForm, ConfirmDailog, InvoiceForm },

	data: () => ({
		search: '',
		isLoading: false,
		formDialog: false,
		invoiceDialog: false,
		isEditMode: false,
		invoiceKey: 0,
		updateAgent: {
			name: '',
			age: '',
			gender: '',
			identity: '',
			identity_number: '',
		},
		agent: null,
		selected: [],
		result: [],
	}),

	computed: {
		...agentsComputed,
		headers() {
			return [
				{ text: '', value: 'id' },
				{ text: ' ', value: 'name' },
				{ text: ' ', value: 'age' },
				{ text: ' ', value: 'identity' },
				{ text: ' ', value: 'travel_type' },
				{ text: '', value: 'actions', sortable: false },
			];
		},
		formTitle() {
			return !this.isEditMode ? ' ' : ' ';
		},
		selectedAgents() {
			// eslint-disable-next-line vue/no-side-effects-in-computed-properties
			this.result = [];

			const agents = this.selected.length ? this.selected : this.agent ? [this.agent] : [];
			agents.forEach((agent) => {
				this.result.push({
					...agent,
					tests: [],
				});
			});

			return this.result;
		},
	},

	async created() {
		try {
			this.isLoading = true;
			await this.getAgentsAction();
			this.isLoading = false;
		} catch (error) {
			this.$VAlert.error('  !');
			this.isLoading = false;
		}
	},

	methods: {
		...agentsActions,
		...invoicesActions,
		editAgent(agent) {
			this.isEditMode = true;
			Object.assign(this.updateAgent, agent);
			this.formDialog = true;
		},
		async deleteAgent(agent) {
			try {
				const confirmed = await this.$refs.confirm.open(' ', '      ');
				if (confirmed) {
					await this.deleteAgentAction(agent.id);
					this.$VAlert.success('  ');
				}
			} catch (error) {
				this.$VAlert.error('  !');
			}
		},
		async saveAgent(agent) {
			try {
				if (this.isEditMode) {
					const agentId = this.updateAgent.id;
					await this.editAgentAction({ ...agent.data, agentId });
				} else {
					const data = await this.saveAgentAction(agent.data);
					this.agent = agent.withInvoice ? data : null;
					this.invoiceDialog = agent.withInvoice;
				}

				this.$VAlert.success('  ');
			} catch (error) {
				this.$VAlert.error('  !');
			}
		},
		async saveInvoice({ data, withPrint }) {
			try {
				const { id } = await this.saveInvoiceAction(data);
				this.$VAlert.success('  ');
				if (withPrint) {
					printJS({
						printable: `/print/invoice/${id}`,
						type: 'pdf',
					});
				}
			} catch (error) {
				this.$VAlert.error('  !');
			}
		},
		printQRCode() {},
		openInvoice() {
			this.invoiceKey++;
			this.invoiceDialog = true;
		},
		closeForm() {
			this.$refs.agentForm.reset();
			this.isEditMode = false;
			this.formDialog = false;
		},
		closeInvoiceForm() {
			this.$refs.invoiceForm.reset();
			this.invoiceDialog = false;
		},
	},
};
</script>

<style lang="scss" scoped></style>
