<template>
	<v-form ref="agentForm" v-model="validForm" @submit.prevent="submited">
		<v-card>
			<v-card-title>
				<span class="headline">{{ formTitle }}</span>
			</v-card-title>
			<v-divider></v-divider>
			<v-card-text>
				<v-row>
					<v-col class="py-0" sm="6" cols="12">
						<v-radio-group v-model="agentType" class="mt-1 mb-3" mandatory row>
							<template v-slot:label>
								<div>  :</div>
							</template>
							<v-radio label="" value="1"></v-radio>
							<v-radio label="  " value="2"></v-radio>
						</v-radio-group>
					</v-col>
					<v-col v-if="agentType == 2" class="py-0" sm="6" cols="12">
						<v-select v-model="companyId" label="" :rules="rules.company" :items="companiesList" dense outlined></v-select>
					</v-col>
				</v-row>
				<v-divider class="mt-2"></v-divider>
				<label class="d-block mt-3 mb-5 ms-1"> </label>
				<v-row class="pt-2">
					<v-col class="py-0" sm="6" cols="12">
						<v-text-field v-model="formData.name" :rules="rules.name" dense outlined label=" "></v-text-field>
					</v-col>
					<v-col class="py-0" sm="6" cols="12">
						<div class="d-flex">
							<v-text-field v-model="formData.age" :rules="rules.age" dense outlined label=" " type="number"></v-text-field>
							<v-select
								v-model="formData.gender"
								:items="genders"
								:rules="rules.gender"
								dense
								outlined
								class="ms-3"
								label=" "
							></v-select>
						</div>
					</v-col>
					<v-col class="py-0" sm="6" cols="12">
						<v-select v-model="formData.identity" :items="identitys" :rules="rules.identity" dense outlined label=" "></v-select>
					</v-col>
					<v-col class="py-0" sm="6" cols="12">
						<v-text-field
							v-model="formData.identity_number"
							type="number"
							:rules="rules.identity_number"
							dense
							outlined
							label=" "
						></v-text-field>
					</v-col>
					<v-col class="py-0" sm="6" cols="12">
						<v-select
							v-model="formData.travel_type"
							:items="travelTypes"
							:rules="rules.travel_type"
							dense
							outlined
							label=" "
						></v-select>
					</v-col>
				</v-row>
			</v-card-text>
			<v-divider></v-divider>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn color="blue darken-1" text @click="close"></v-btn>
				<v-btn color="blue darken-1" type="submit" :disabled="!validForm" text>
					<span></span>
				</v-btn>
				<v-btn color="blue darken-1" type="submit" :disabled="!validForm" text @click="withInvoice = true">  </v-btn>
			</v-card-actions>
		</v-card>
	</v-form>
</template>

<script>
import { companiesActions } from '../../state/mapper';

export default {
	name: 'AgentForm',
	props: {
		formTitle: {
			type: String,
			default: 'Make new agent',
		},
		agent: {
			type: Object,
			default: () => ({
				name: '',
				age: '',
				gender: '',
				travel_type: '',
				identity: '',
				identity_number: '',
			}),
		},
		isEdit: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			validForm: false,
			showPass: false,
			withInvoice: false,
			agentType: '1',
			companies: [],
			companyId: 0,
			genders: [
				{ text: '', value: 1 },
				{ text: '', value: 2 },
			],
			identitys: [
				{ text: '', value: 'Personal' },
				{ text: ' ', value: 'Passport' },
			],
			travelTypes: [
				{ text: '', value: '' },
				{ text: '', value: '' },
				{ text: '', value: '' },
			],
		};
	},
	computed: {
		rules() {
			return {
				name: [(val) => !!val || '  '],
				company: [(val) => !!val || '  '],
				age: [(value) => !!value || '  '],
				gender: [(val) => !!val || '  '],
				travel_type: [(val) => !!val || '   '],
				identity: [(val) => !!val || '   '],
				identity_number: [(value) => !!value || '  '],
			};
		},
		formData() {
			return this.agent;
		},
		companiesList() {
			return this.companies.map((company) => ({
				text: company.name,
				value: company.id,
			}));
		},
	},
	watch: {
		agent: {
			handler(val) {
				if (val.company_id) {
					this.agentType = '2';
					this.companyId = val.company_id;
				}
			},
			immediate: true,
			deep: true,
		},
	},
	async created() {
		this.companies = await this.loadCompanies();
	},
	methods: {
		loadCompanies: companiesActions.getCompaniesAction,
		submited() {
			this.$emit('submited', {
				data: {
					...this.formData,
					company_id: this.agentType == 2 ? this.companyId : null,
				},
				withInvoice: this.isEdit ? false : this.withInvoice,
			});

			this.close();
		},
		close() {
			this.$refs.agentForm.reset();
			this.$emit('canceled');
		},
		reset() {
			this.$refs.agentForm.reset();
		},
	},
};
</script>

<style lang="scss" scoped></style>
